/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
body {
    font-family: "Open Sans", sans-serif;
    color: #272829;
  }
  
  .row .column img{
    width: 100%;
    height: auto;
  }
  
  :root {
    --main-color: coral;
    --main-color-hover: rgba(255, 127, 80, 0.753);
    font-size:18px;
  }
  
  a {
    color: var(--main-color);
  }
  
  a:hover {
    color:var(--main-color-hover);
    text-decoration: none;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: "Raleway", sans-serif;
  }
  
  /*--------------------------------------------------------------
  # Back to top button
  --------------------------------------------------------------*/
  .back-to-top {
    position: fixed;
    display: none;
    width: 2.2222rem;
    height: 2.2222rem;
    border-radius: 2.7778rem;
    right: 0.8333rem;
    bottom: 0.8333rem;
    background: var(--main-color);
    color: #fff;
    transition: display 0.5s ease-in-out;
    z-index: 99999;
  }
  
  .back-to-top i {
    font-size: 1.3333rem;
    position: absolute;
    top: 0.3889rem;
    left: 0.4444rem;
  }
  
  .back-to-top:hover {
    color: #fff;
    background: #0678e3;
    transition: background 0.2s ease-in-out;
  }
  
  /*--------------------------------------------------------------
  # Preloader
  --------------------------------------------------------------*/
  #preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    overflow: hidden;
    background: #fff;
  }
  
  #preloader:before {
    content: "";
    position: fixed;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
    border: 0.3333rem solid var(--main-color);
    border-top-color: #fff;
    border-bottom-color: #fff;
    border-radius: 50%;
    width: 3.3333rem;
    height: 3.3333rem;
    -webkit-animation: animate-preloader 1s linear infinite;
    animation: animate-preloader 1s linear infinite;
  }
  
  @-webkit-keyframes animate-preloader {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes animate-preloader {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  /*--------------------------------------------------------------
  # Disable aos animation delay on mobile devices
  --------------------------------------------------------------*/
  @media screen and (max-width: 42.6667rem) {
    [data-aos-delay] {
      transition-delay: 0 !important;
    }
  }
  
  /*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/
  #header {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 9997;
    transition: all 0.5s;
    padding: 0.8333rem;
    overflow-y: auto;
  }
  
  @media (max-width: 55.1111rem) {
    #header {
      width: 16.6667rem;
      background: #fff;
      border-right: 0.0556rem solid #e6e9ec;
      left: -16.6667rem;
    }
  }
  
  @media (min-width: 55.1111rem) {
    #main {
      margin-left: 5.5556rem;
    }
  }
  
  /*--------------------------------------------------------------
  # Navigation Menu
  --------------------------------------------------------------*/
  /* Desktop Navigation */
  .nav-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .nav-menu > ul > li {
    position: relative;
    white-space: nowrap;
  }
  
  .nav-menu a {
    display: flex;
    align-items: center;
    color: #45505b;
    padding: 0.5556rem 1rem;
    margin-bottom: 0.4444rem;
    transition: 0.3s;
    font-size: 0.8333rem;
    border-radius: 2.7778rem;
    background: #f2f3f5;
    height: 3.1111rem;
    width: 100%;
    overflow: hidden;
    transition: 0.3s;
  }
  
  .nav-menu a i {
    font-size: 1.1111rem;
  }
  
  .nav-menu a span {
    padding: 0 0.2778rem 0 0.3889rem;
    color: #45505b;
  }
  
  @media (min-width: 55.1111rem) {
    .nav-menu a {
      width: 3.1111rem;
    }
    .nav-menu a span {
      display: none;
      color: #fff;
    }
  }
  
  .nav-menu a:hover, .nav-menu .active > a, .nav-menu li:hover > a {
    color: #fff;
    background: var(--main-color);
  }
  
  .nav-menu a:hover span, .nav-menu .active > a span, .nav-menu li:hover > a span {
    color: #fff;
  }
  
  .nav-menu a:hover, .nav-menu li:hover > a {
    width: 100%;
    color: #fff;
  }
  
  .nav-menu a:hover span, .nav-menu li:hover > a span {
    display: block;
  }
  
  /* Mobile Navigation */
  .mobile-nav-toggle {
    position: fixed;
    right: 0.8333rem;
    top: 0.8333rem;
    z-index: 9998;
    border: 0;
    background: none;
    font-size: 1.3333rem;
    transition: all 0.4s;
    outline: none !important;
    line-height: 1;
    cursor: pointer;
    text-align: right;
  }
  
  .mobile-nav-toggle i {
    color: #45505b;
  }
  
  .mobile-nav-active {
    overflow: hidden;
  }
  
  .mobile-nav-active #header {
    left: 0;
  }
  
  .mobile-nav-active .mobile-nav-toggle i {
    color: var(--main-color);
  }
  
  /*--------------------------------------------------------------
  # Hero Section
  --------------------------------------------------------------*/
  #hero {
    width: 100%;
    height: 100vh;
    // background: url("../img/hero-bg.jpg") top right no-repeat;
    background-size: cover;
    position: relative;
  }
  
  @media (min-width: 55.1111rem) {
    #hero {
      padding-left: 8.8889rem;
    }
  }
  
  #hero:before {
    content: "";
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
  }
  
  
  
  #hero h1 {
    margin: 0;
    font-size: 3.5556rem;
    font-weight: 700;
    line-height: 3.1111rem;
    color: #45505b;
  }
  
  #hero p {
    color: #45505b;
    margin: 0.8333rem 0 0 0;
    font-size: 1.4444rem;
    font-family: "Poppins", sans-serif;
  }
  
  #hero p span {
    color: var(--main-color);
    letter-spacing: 0.0556rem;
  }
  
  #hero .social-links {
    margin-top: 1.6667rem;
  }
  
  #hero .social-links a {
    font-size: 1.3333rem;
    display: inline-block;
    color: #45505b;
    line-height: 1;
    margin-right: 1.1111rem;
    transition: 0.3s;
  }
  
  #hero .social-links a:hover {
    color: var(--main-color);
  }
  
  @media (max-width: 55.1111rem) {
    #hero {
      text-align: center;
    }
    #hero h1 {
      font-size: 1.7778rem;
      line-height: 2rem;
    }
    #hero p {
      margin-top: 0.5556rem;
      font-size: 1.1111rem;
      line-height: 1.3333rem;
    }
  }
  
  /*--------------------------------------------------------------
  # Sections General
  --------------------------------------------------------------*/
  section {
    padding: 3.3333rem 0;
    overflow: hidden;
  }
  section.inner-section {
    padding: 0;
  }
  .section-title {
    text-align: center;
    padding-bottom: 1.6667rem;
  }
  
  .section-title h2 {
    font-size: 1.7rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1.1111rem;
    padding-bottom: 1.1111rem;
    position: relative;
    color: #45505b;
  }
  
  .section-title h2::before {
    content: '';
    position: absolute;
    display: block;
    width: 6.6667rem;
    height: 0.0556rem;
    background: #ddd;
    bottom: 0.0556rem;
    left: calc(50% - 60px);
  }
  
  .section-title h2::after {
    content: '';
    position: absolute;
    display: block;
    width: 2.2222rem;
    height: 0.1667rem;
    background: var(--main-color);
    bottom: 0;
    left: calc(50% - 20px);
  }
  
  .section-title p {
    margin-bottom: 0;
  }
  
  
  
  /*--------------------------------------------------------------
  # Facts
  --------------------------------------------------------------*/
  .facts .count-box {
    padding: 1.6667rem 1.6667rem 1.3889rem 1.6667rem;
    margin-top: 1.6667rem;
    width: 100%;
    position: relative;
    text-align: center;
    background: #fff;
  }
  
  .facts .count-box i {
    position: absolute;
    top: -1.3889rem;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1.3333rem;
    background: var(--main-color);
    padding: 0.6667rem;
    color: #fff;
    border-radius: 2.7778rem;
  }
  
  .facts .count-box span {
    font-size: 2rem;
    display: block;
    font-weight: 600;
    color: #011426;
  }
  
  .facts .count-box p {
    padding: 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    font-size: 0.7778rem;
  }
  
  /*--------------------------------------------------------------
  # Akills
  --------------------------------------------------------------*/
  .skills .progress {
    height: 2.7778rem;
    display: block;
    background: none;
  }
  
  .skills .progress .skill {
    padding: 0.5556rem 0;
    margin: 0 0 0.3333rem 0;
    text-transform: uppercase;
    display: block;
    font-weight: 600;
    font-family: "Poppins", sans-serif;
    color: #45505b;
  }
  
  .skills .progress .skill .val {
    float: right;
    font-style: normal;
  }
  
  .skills .progress-bar-wrap {
    background: #f2f3f5;
  }
  
  .skills .progress-bar {
    width: 0.0556rem;
    height: 0.5556rem;
    transition: .9s;
    background-color: var(--main-color);
  }
  
  /*--------------------------------------------------------------
  # Resume
  --------------------------------------------------------------*/
  .resume .resume-title {
    font-size: 1.4444rem;
    font-weight: 700;
    margin-top: 1.1111rem;
    margin-bottom: 1.1111rem;
    color: #45505b;
  }
  
  .resume .resume-item {
    padding: 0 0 1.1111rem 1.1111rem;
    margin-top: -0.1111rem;
    border-left: 0.1111rem solid var(--main-color);
    position: relative;
  }
  
  .resume .resume-item h4 {
    line-height: 1rem;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: var(--main-color);
    margin-bottom: 0.5556rem;
  }
  
  .resume .resume-item h5 {
    font-size: 0.8889rem;
    background: #f7f8f9;
    padding: 0.2778rem 0.8333rem;
    display: inline-block;
    font-weight: 600;
    margin-bottom: 0.5556rem;
  }
  
  .resume .resume-item ul {
    padding-left: 1.1111rem;
  }
  
  .resume .resume-item ul li {
    padding-bottom: 0.5556rem;
  }
  
  .resume .resume-item:last-child {
    padding-bottom: 0;
  }
  
  .resume .resume-item::before {
    content: "";
    position: absolute;
    width: 0.8889rem;
    height: 0.8889rem;
    border-radius: 2.7778rem;
    left: -0.5rem;
    top: 0;
    background: #fff;
    border: 0.1111rem solid var(--main-color);
  }
  
  /*--------------------------------------------------------------
  # Portfolio
  --------------------------------------------------------------*/
  .portfolio .portfolio-item {
    margin-bottom: 1.6667rem;
  }
  
  .portfolio #portfolio-flters {
    padding: 0;
    margin: 0 auto 1.3889rem auto;
    list-style: none;
    text-align: center;
    background: #fff;
    border-radius: 2.7778rem;
    padding: 0.1111rem 0.8333rem;
  }
  
  .portfolio #portfolio-flters li {
    cursor: pointer;
    display: inline-block;
    padding: 0.5556rem 0.8333rem;
    font-size: 0.7778rem;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
    color: #272829;
    margin-bottom: 0.2778rem;
    transition: all 0.3s ease-in-out;
  }
  
  .portfolio #portfolio-flters li:hover, .portfolio #portfolio-flters li.filter-active {
    color: var(--main-color);
  }
  
  .portfolio #portfolio-flters li:last-child {
    margin-right: 0;
  }
  
  .portfolio .portfolio-wrap {
    transition: 0.3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    background: rgba(69, 80, 91, 0.8);
  }
  
  .portfolio .portfolio-wrap::before {
    content: "";
    background: rgba(255, 255, 255, 0.7);
    position: absolute;
    left: 1.6667rem;
    right: 1.6667rem;
    top: 1.6667rem;
    bottom: 1.6667rem;
    transition: all ease-in-out 0.3s;
    z-index: 2;
    opacity: 0;
  }
  
  .portfolio .portfolio-wrap .portfolio-info {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    z-index: 3;
    transition: all ease-in-out 0.3s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .portfolio .portfolio-wrap .portfolio-info::before {
    display: block;
    content: "";
    width: 2.6667rem;
    height: 2.6667rem;
    position: absolute;
    top: 1.9444rem;
    left: 1.9444rem;
    border-top: 0.1667rem solid #d7dce1;
    border-left: 0.1667rem solid #d7dce1;
    transition: all 0.5s ease 0s;
    z-index: 9994;
  }
  
  .portfolio .portfolio-wrap .portfolio-info::after {
    display: block;
    content: "";
    width: 2.6667rem;
    height: 2.6667rem;
    position: absolute;
    bottom: 1.9444rem;
    right: 1.9444rem;
    border-bottom: 0.1667rem solid #d7dce1;
    border-right: 0.1667rem solid #d7dce1;
    transition: all 0.5s ease 0s;
    z-index: 9994;
  }
  
  .portfolio .portfolio-wrap .portfolio-info h4 {
    font-size: 1.1111rem;
    color: #45505b;
    font-weight: 600;
  }
  
  .portfolio .portfolio-wrap .portfolio-info p {
    color: #45505b;
    font-size: 0.7778rem;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
  }
  
  .portfolio .portfolio-wrap .portfolio-links {
    text-align: center;
    z-index: 4;
  }
  
  .portfolio .portfolio-wrap .portfolio-links a {
    color: #45505b;
    margin: 0 0.1111rem;
    font-size: 1.5556rem;
    display: inline-block;
    transition: 0.3s;
  }
  
  .portfolio .portfolio-wrap .portfolio-links a:hover {
    color: #148af9;
  }
  
  .portfolio .portfolio-wrap:hover::before {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
  }
  
  .portfolio .portfolio-wrap:hover .portfolio-info {
    opacity: 1;
  }
  
  .portfolio .portfolio-wrap:hover .portfolio-info::before {
    top: 0.8333rem;
    left: 0.8333rem;
  }
  
  .portfolio .portfolio-wrap:hover .portfolio-info::after {
    bottom: 0.8333rem;
    right: 0.8333rem;
  }
  
  /*--------------------------------------------------------------
  # Services
  --------------------------------------------------------------*/
  .services .icon-box {
    text-align: center;
    padding: 3.8889rem 1.1111rem 4.4444rem 1.1111rem;
    transition: all ease-in-out 0.3s;
    background: #fff;
    box-shadow: 0rem 0.2778rem 5rem 0rem rgba(110, 123, 131, 0.05);
  }
  
  .services .icon-box .icon {
    margin: 0 auto;
    width: 5.5556rem;
    height: 5.5556rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: ease-in-out 0.3s;
    position: relative;
  }
  
  .services .icon-box .icon i {
    font-size: 2rem;
    transition: 0.5s;
    position: relative;
  }
  
  .services .icon-box .icon svg {
    position: absolute;
    top: 0;
    left: 0;
  }
  
  .services .icon-box .icon svg path {
    transition: 0.5s;
    fill: #f5f5f5;
  }
  
  .services .icon-box h4 {
    font-weight: 600;
    margin: 0.5556rem 0 0.8333rem 0;
    font-size: 1.2222rem;
  }
  
  .services .icon-box h4 a {
    color: #45505b;
    transition: ease-in-out 0.3s;
  }
  
  .services .icon-box p {
    line-height: 1.3333rem;
    font-size: 0.7778rem;
    margin-bottom: 0;
  }
  
  .services .icon-box:hover {
    border-color: #fff;
    box-shadow: 0rem 0 1.9444rem 0 rgba(0, 0, 0, 0.08);
  }
  
  .services .iconbox-blue i {
    color: #47aeff;
  }
  
  .services .iconbox-blue:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-blue:hover .icon path {
    fill: #47aeff;
  }
  
  .services .iconbox-orange i {
    color: #ffa76e;
  }
  
  .services .iconbox-orange:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-orange:hover .icon path {
    fill: #ffa76e;
  }
  
  .services .iconbox-pink i {
    color: #e80368;
  }
  
  .services .iconbox-pink:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-pink:hover .icon path {
    fill: #e80368;
  }
  
  .services .iconbox-yellow i {
    color: #ffbb2c;
  }
  
  .services .iconbox-yellow:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-yellow:hover .icon path {
    fill: #ffbb2c;
  }
  
  .services .iconbox-red i {
    color: #ff5828;
  }
  
  .services .iconbox-red:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-red:hover .icon path {
    fill: #ff5828;
  }
  
  .services .iconbox-teal i {
    color: #11dbcf;
  }
  
  .services .iconbox-teal:hover .icon i {
    color: #fff;
  }
  
  .services .iconbox-teal:hover .icon path {
    fill: #11dbcf;
  }
  
  /*--------------------------------------------------------------
  # Testimonials
  --------------------------------------------------------------*/
  .testimonials .section-header {
    margin-bottom: 2.2222rem;
  }
  
  .testimonials .testimonial-item {
    text-align: center;
  }
  
  .testimonials .testimonial-item .testimonial-img {
    width: 6.6667rem;
    border-radius: 50%;
    border: 0.2222rem solid #fff;
    margin: 0 auto;
  }
  
  .testimonials .testimonial-item h3 {
    font-size: 1.1111rem;
    font-weight: bold;
    margin: 0.5556rem 0 0.2778rem 0;
    color: #111;
  }
  
  .testimonials .testimonial-item h4 {
    font-size: 0.7778rem;
    color: #999;
    margin: 0 0 0.8333rem 0;
  }
  
  .testimonials .testimonial-item .quote-icon-left, .testimonials .testimonial-item .quote-icon-right {
    color: #90c8fc;
    font-size: 1.4444rem;
  }
  
  .testimonials .testimonial-item .quote-icon-left {
    display: inline-block;
    left: -0.2778rem;
    position: relative;
  }
  
  .testimonials .testimonial-item .quote-icon-right {
    display: inline-block;
    right: -0.2778rem;
    position: relative;
    top: 0.5556rem;
  }
  
  .testimonials .testimonial-item p {
    font-style: italic;
    margin: 0 auto 0.8333rem auto;
  }
  
  .testimonials .owl-nav, .testimonials .owl-dots {
    margin-top: 0.2778rem;
    text-align: center;
  }
  
  .testimonials .owl-dot {
    display: inline-block;
    margin: 0 0.2778rem;
    width: 0.6667rem;
    height: 0.6667rem;
    border-radius: 50%;
    background-color: #ddd !important;
  }
  
  .testimonials .owl-dot.active {
    background-color: var(--main-color) !important;
  }
  
  @media (min-width: 55.1111rem) {
    .testimonials .testimonial-item p {
      width: 80%;
    }
  }
  
  /*--------------------------------------------------------------
  # Contact
  --------------------------------------------------------------*/
  .contact .info {
    width: 100%;
    background: #fff;
  }
  
  .contact .info i {
    font-size: 1.1111rem;
    color: var(--main-color);
    float: left;
    width: 2.4444rem;
    height: 2.4444rem;
    background: #eef7ff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2.7778rem;
    transition: all 0.3s ease-in-out;
  }
  
  .contact .info h4 {
    padding: 0 0 0 3.3333rem;
    font-size: 1.2222rem;
    font-weight: 600;
    margin-bottom: 0.2778rem;
    color: #45505b;
  }
  
  .contact .info p {
    padding: 0 0 0 3.3333rem;
    margin-bottom: 0;
    font-size: 0.7778rem;
    color: #728394;
  }
  
  .contact .info .email, .contact .info .phone {
    margin-top: 2.2222rem;
  }
  
  .contact .info .email:hover i, .contact .info .address:hover i, .contact .info .phone:hover i {
    background: var(--main-color);
    color: #fff;
  }
  
  .contact .php-email-form {
    width: 100%;
    background: #fff;
  }
  
  .contact .php-email-form .form-group {
    padding-bottom: 0.4444rem;
  }
  
  .contact .php-email-form .validate {
    display: none;
    color: red;
    margin: 0 0 0.8333rem 0;
    font-weight: 400;
    font-size: 0.7222rem;
  }
  
  .contact .php-email-form .error-message {
    display: none;
    color: #fff;
    background: #ed3c0d;
    text-align: left;
    padding: 0.8333rem;
    font-weight: 600;
  }
  
  .contact .php-email-form .error-message br + br {
    margin-top: 1.3889rem;
  }
  
  .contact .php-email-form .sent-message {
    display: none;
    color: #fff;
    background: #18d26e;
    text-align: center;
    padding: 0.8333rem;
    font-weight: 600;
  }
  
  .contact .php-email-form .loading {
    display: none;
    background: #fff;
    text-align: center;
    padding: 0.8333rem;
  }
  
  .contact .php-email-form .loading:before {
    content: "";
    display: inline-block;
    border-radius: 50%;
    width: 1.3333rem;
    height: 1.3333rem;
    margin: 0 0.5556rem -0.3333rem 0;
    border: 0.1667rem solid #18d26e;
    border-top-color: #eee;
    -webkit-animation: animate-loading 1s linear infinite;
    animation: animate-loading 1s linear infinite;
  }
  
  .contact .php-email-form input, .contact .php-email-form textarea {
    border-radius: 0.2222rem;
    box-shadow: none;
    font-size: 0.7778rem;
  }
  
  .contact .php-email-form input {
    height: 2.4444rem;
  }
  
  .contact .php-email-form textarea {
    padding: 0.5556rem 0.6667rem;
  }
  
  .contact .php-email-form button[type="submit"] {
    background: var(--main-color);
    border: 0;
    padding: 0.5556rem 1.9444rem;
    color: #fff;
    transition: 0.4s;
    border-radius: 2.7778rem;
  }
  
  .contact .php-email-form button[type="submit"]:hover {
    background: #0678e3;
  }
  
  @-webkit-keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
